.blockCalendar {
  margin-top: 2%;
  display: flex;
  gap: 10px;
  align-items: center;
  padding: 20px 50px;
  overflow-x: auto;
}
::-webkit-scrollbar {
  width: 24px;
  height: 8px;
  background-color: #016a70;
}

::-webkit-scrollbar-thumb {
  background-color: grey;
  border-radius: 9em;
  box-shadow: inset 1px 1px 10px #f3faf7;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #253861;
}
