.modalWrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.modalBlock {
  background: whitesmoke;
  padding: 25px;
  border: 3px solid black;
  border-radius: 5px;
  width: 35%;
}
